import { useState } from 'react';
import { uploadFile, STORAGE_FOLDERS } from '../config/vercel-blob';

export const useImageUpload = () => {
  const [uploading, setUploading] = useState(false);
  const [error, setError] = useState(null);

  const uploadImage = async (file, path = 'profile-photos') => {
    if (!file) return null;

    setUploading(true);
    setError(null);

    try {
      // Map path to storage folder
      let storageFolder = path.replace('_', '-');

      // Default to profile-photos if path is empty
      if (!storageFolder || storageFolder === 'undefined') {
        storageFolder = STORAGE_FOLDERS.PROFILE_PHOTOS;
      }

      // Upload to Vercel Blob
      const blob = await uploadFile(file, storageFolder);

      setUploading(false);
      return blob.url;
    } catch (err) {
      console.error("Image upload failed:", err);
      setError(err.message || 'Failed to upload image');
      setUploading(false);
      return null;
    }
  };

  return { uploadImage, uploading, error };
};
